<template>
	<div class="prototype_phylogeny_block cell_block"
		v-bind:style="css_style"
		v-on:click.stop="on_select_block()"

		v-bind:class="{is_selected: is_first_selected, is_visible: is_selected}"
	>
		<div class="block" v-bind:class="{is_parent_block: (primary_children.length == 0)}">
			<div class="count" v-if="block_label">{{block_label}}</div>
		</div>
		<div class="block_info"><!--  v-if="children || is_selected" -->

			<div class="info" v-if="children || is_first_selected">
				<div class="info_container">
					<template v-if="show_end_block">
						<div class="label">Aggregated Totals:</div>
						<div class="value">
							<div class="group group_red">M: {{parents_aggregate.t1_mutations}}</div>
						</div>
						<div class="value">
							<div class="group group_blue">G: {{parents_aggregate.t1_cgc_genes}}</div>
						</div>
						<div class="value">
							<div class="group">D: {{parents_aggregate.drugs}}</div>
						</div>
					</template>

					<template v-if="!show_end_block">
						<div class="label">M:</div>
						<div class="value">
							<div class="group group_red">{{json_data.t1_mutations}}</div>
							<div>{{json_data.mutations}}</div>
						</div>

						<div class="label">G:</div>
						<div class="value">
							<div class="group group_blue">{{json_data.t1_cgc_genes}}</div>
							<div>{{json_data.genes}}</div>
						</div>

						<div class="label">D:</div>
						<div class="value">
							<div class="group">{{json_data.drugs}}</div>
						</div>
					</template>
				</div>

				<div class="summary" v-if="json_data && typeof json_data.summary != 'undefined'">
					<div class="summary_header">Mutation Edge Info</div>

					<div class="summary_table">
						<table>
							<thead>
								<tr>
									<th v-for="(column,index) in summary_columns" v-bind:class="['column_' + index]">
										{{column}}
									</th>
								</tr>
							</thead>

							<tbody>
								<tr v-for="(data) in json_data.summary">
									<td v-for="(column,index) in summary_columns" v-bind:class="['column_' + index]">
										{{data[index]}}
									</td>
								</tr>
							</tbody>
						</table>
					</div>

					<div class="summary_button" v-on:click.stop="on_toggle_table()">{{(!global.phylogeny_show_table) ? 'Open in' : 'Hide'}} Table</div>
				</div>
			</div>

			<div class="block_background" v-if="children">
				<svg width="100%" height="100%" viewBox="0 0 100 100" preserveAspectRatio="none">
					<linearGradient x1="0" y1="0" x2="100%" y2="0" id="gradient">
						<stop class="gradient_start" offset="0" />
						<stop class="gradient_end" offset="100%" />
					</linearGradient>

					<path class="" style="stroke-miterlimit:10;stoke-width:5;stroke-linejoin=round;" fill="url(#gradient)" d="M0 0 C 0 20, 100 20, 100 0 L 100 100 C 100 80, 0 80, 0 100 L 0 0" vector-effect="non-scaling-stroke"></path>
				</svg>
			</div>
		</div>

		<div class="children" v-if="primary_children">
			<prototype_phylogeny_block v-for="(data,index) in primary_children"
				v-bind:json_data="data"

				v-bind:sample="(data.sample) ? data.sample : 'primary'"
				v-bind:label="data.label"
				v-bind:color="data.color"

				v-bind:genes="(data.genes) ? data.genes : 0"
				v-bind:parent="get_this_block()"
				v-bind:parent_total="children_total"
				v-bind:show_end_block="(data.show_end_block) ? data.show_end_block : (!data.children || data.children.length == 0)"

				v-bind:children="data.children"
				v-bind:block_level="block_level + 1"

				v-bind:key="index"
			></prototype_phylogeny_block>
		</div>

		<div class="end_block" v-if="show_end_block"><!--  || !children -->
<!--			<div class="name">1</div>-->

			<div class="percentage">{{json_data.proportion}}%</div>
		</div>
	</div>
</template>

<script>
//*********************************************************

import Vue from 'vue';
//import {format, startOfToday} from 'date-fns'; //{format, formatDistance, formatRelative, subDays}

import EventBus from '../../event_bus/event_bus.js';

export default {
	name: 'prototype_phylogeny_block',
	components: {
	},
	props: {
		json_data: {type: Object},

		sample: {type: String},
		label: {type: String},
		color: {type: String},
		genes: {type: Number},

		parent: {type: Object},
		parent_total: {type: Number},

		block_level: {type: Number, default: 1},
		show_end_block: {type: Boolean},

		children: {type: Array},

		//used relapse layout
		block_label: {type: String},
		block_list: {type: Array},

		//override click
		on_click: {type: Function}
	},
	computed: {
		parents_aggregate: function() {
			let totals = {
				't1_mutations': 0,
				'mutations': 0,
				't1_cgc_genes': 0,
				'genes': 0,
				'drugs': 0,
			};

			//walk up each parent and create aggregate
			let parents = this.get_all_parents();

			for(let i=0; i<parents.length; i++) {
				let parent_data = parents[i];

				totals['t1_mutations'] += parent_data._props.json_data.t1_mutations;
				totals['mutations'] += parent_data._props.json_data.mutations;
				totals['t1_cgc_genes'] += parent_data._props.json_data.t1_cgc_genes;
				totals['genes'] += parent_data.genes;
				totals['drugs'] += parent_data._props.json_data.drugs;
			}

			return totals;
		},
		primary_children: function() {
			//only return children that are sample = primary
			let children = (this.children) ? this.children : [];

			let data = children.filter((item)=>{
				return (item.sample == 'primary') ? true : false;
			});

			return data;
		},
		relapse_children: function() {
			//only return children that are sample = relapse
			let children = (this.children) ? this.children : [];

			let data = children.filter((item)=>{
				return (item.sample == 'relapse') ? true : false;
			});

			return data;
		},
		is_first_selected: function() {
			//determine if this is the first in the selected array (which is the one clicked on)

			//reference this variable so this computed value will update when this variable does
			//https://logaretm.com/blog/2019-10-11-forcing-recomputation-of-computed-properties
			this.global.selected_blocks;

			return (this.global.selected_blocks.length > 0 && this.global.selected_blocks[0] == this) ? true : false;
		},
		is_selected: function() {
			//consider it selected if in the selected array (selected or parent of)

			//reference this variable so this computed value will update when this variable does
			//https://logaretm.com/blog/2019-10-11-forcing-recomputation-of-computed-properties
			this.global.selected_blocks;

			return (this.global.selected_blocks.indexOf(this) > -1) ? true : false;
		},
		css_style: function() {
			//reference this variable so this computed value will update when this variable does
			//https://logaretm.com/blog/2019-10-11-forcing-recomputation-of-computed-properties
			this.is_selected;

			//get children color (assume first is the same as all of them)
			let start_color = (this.color) ? this.color : '#555';
			let children_color = (this.children && typeof this.children[0].color != 'undefined') ? this.children[0].color : start_color;

			let styles = {
				'--cell_block_color': (this.color) ? this.color : '#555',

				'--gradient_start': start_color,
				'--gradient_end': children_color,
			};

			//only apply the height for the final tree end
			if (this.parent_total) {
				let has_selected_block = (this.global.selected_blocks.length > 0) ? true : false;
				let selected_block_level = (has_selected_block) ? this.global.selected_blocks[0].block_level : 0;

				styles['flex'] = (has_selected_block && selected_block_level >= this.block_level && !this.is_selected) ? '1' : '' + (this.json_data.proportion * 1000); //proper value is 100, using 1000 to force this to shrink super small when selected
				//styles['flex'] = (has_selected_block && selected_block_level >= this.block_level && !this.is_selected) ? '1' : '' + (this.genes / this.parent_total * 1000); //proper value is 100, using 1000 to force this to shrink super small when selected
			}

			return styles;
		},
		children_total: function() {
			let total = 0;

			if (this.children) {
				for (let i=0; i < this.children.length; i++) {
					if (typeof this.children[i].proportion != 'undefined') {
						total += this.children[i].proportion;
					}
				}
			}

			//return the total OR the number of genes specified
			return total; //(this.children) ? total : this.genes;
		}
	},
	data() {
		return {
			summary_columns: {
				"tier": "Tier",
				"type": "Type",
				"mutations": "Mutations",
				"cgc_genes": "CGC Genes",
				"drugs": "Drugs"
			},

			//global data
			global: this.$root._data
		}
	},
	methods: {

		//********************************************
		on_select_block() {
			//determine if we override the on_click
			if (this.on_click) {
				this.on_click(this);
				return;
			}

			//globally change the indicate which blocks are selected (or none at all)
			if (this.global.selected_blocks && this.global.selected_blocks[0] == this) {
				//deselect this block
				this.global.selected_blocks = [];
			} else {
				//walk through and find any parent blocks for this selected block
				let selected_blocks = [];

				//make sure this is added (and first in the array)
				selected_blocks.push(this);

				//recursively walk through parents
				selected_blocks = selected_blocks.concat(this.get_all_parents());

				this.global.selected_blocks = selected_blocks;
			}

			//reset table flag
//			this.global.phylogeny_show_table = false;
		},

		//********************************************
		on_toggle_table() {
			this.global.phylogeny_show_table = !this.global.phylogeny_show_table;
		},

		//********************************************
		get_all_parents(list) {
			//default list of parents
			if (typeof list == 'undefined') {list = [];}

			if (this.parent) {
				//add this parent reference
				list.push(this.parent);

				return this.parent.get_all_parents(list);
			} else {
				//return the final list
				return list;
			}
		},

		//********************************************
		get_this_block() {
			return this;
		}
	},
	destroyed() {
	},
	mounted() {

return; //for now, just skip the relapse data

		//do we have any relapse children
		if (this.relapse_children.length > 0) {
			//for now, group by color

			for (let i=0; i<this.relapse_children.length; i++) {
				let item = this.relapse_children[i];

				if (!this.global.phylogeny_relapse_data[item.color]) {
					this.global.phylogeny_relapse_data[item.color] = {
						sample: item.sample,
						label: item.label,
						edge_label: item.edge_label,
						proportion: 0,

						color: item.color,
						show_end_block: false,

						children: null,

						genes: 0,
						gene_total: 0,

						block_list: [],
					};
				}

				this.global.phylogeny_relapse_data[item.color].block_list.push(item);
				this.global.phylogeny_relapse_data[item.color].proportion += item.proportion;
			}
		}

/*
		//if relapse data, add to relapse data list
		if (this.type == 'relapse' && !this.children) {
			//for now, group by color
			if (!this.global.phylogeny_relapse_data[this.color]) {
				this.global.phylogeny_relapse_data[this.color] = {
					sample: this.sample,
					label: this.label,
					color: this.color,
					show_end_block: false,

					children: null,

					genes: 0,
					gene_total: 0,

					block_list: [],
				};
			}

			this.global.phylogeny_relapse_data[this.color].block_list.push(this);
			this.global.phylogeny_relapse_data[this.color].gene_total += this.genes;
		}
*/
	},
	watch: {
	},
	created() {
	}
};

//*********************************************************
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>


